}

.feedback-instance-label {
  margin-bottom: 1rem;
}

.feedback-instance-button, .us-button {
  padding: 0.3rem 1rem;
  margin-right: 1rem;
  /* text-align: center; */
  display: inline-block;
  /* border: 1px solid #333333; */
  background-color: #eee;
  border: 1px solid #eee;
  border-radius: 0.25rem;
  cursor: pointer;
  /* opacity: 0.5; */
}

.feedback-instance-button.selected {
  opacity: 1;
  background-color: #17a2b8 !important;
  border: 1px solid #17a2b8 !important;
  color: white;
}

.feedback-instance-button:hover {
  opacity: 1;
  background-color: #c8eff5;
  border: 1px solid #c8eff5;
}

.us-button {
  color: black;
  text-decoration: none;
  opacity: 1;
  padding: 0.3rem 1rem;
  /* margin: 0 0.5rem; */
  /* opacity: 0.3; */
  /* box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.3); */
