  border-radius: 0;
}

.us-button.ready:hover {
  opacity: 1;
  /* text-decoration: underline; */
}

.us-button.ready.selected {
  opacity: 1;
}

.next-email {
  cursor: not-allowed;
  border-radius: 0.25rem;
  background-color: #eeeeee;
  border: 1px solid #eeeeee;
  /* opacity: 0.7; */
}

.next-email.ready {
  font-size: 1.1rem;
  cursor: pointer;
  background-color: #007bff;
  border: 1px solid #007bff;
  color: white;
  /* opacity: 1; */
}

.standard-button {
  font-size: 1.1rem;
  cursor: pointer;
  background-color: #007bff;
  border: 1px solid #007bff;
  color: white;
  text-decoration: none;
  opacity: 1;
  padding: 0.3rem 1rem;
  border-radius: 0.25rem;
  margin-top: 1rem;
  display: inline-block;
}

.standard-button:hover {
  background-color: #0069d9;
  border-color: #0069d9;
}

.next-email.ready:hover {
  background-color: #0069d9;
  border-color: #0069d9;
}

