.radioset {
  background-color: #eeeeee;
  display: inline-block;
  white-space: nowrap;
  vertical-align: middle;
  /* width: 80%; */
  /* margin: 0 auto; */
  margin-top: 0.3rem;
  text-align: center;
  min-width: 1000px;
  width: 100%;
  padding: 0.3rem 0;
}

.radioset-inner {
  width: 800px;
}

.radioset label {
  min-width: 80px;
  vertical-align: middle;
  display: inline-block;
  text-align: center;
  padding: 0.8rem 0;
  margin: 0 1rem;
}

.radioset input {
  display: block;
  width: 80px;
  /* width: 5vw; */
  margin: 0 auto;
  margin-bottom: 0.3rem;
}

ol {
  counter-reset: item;
}

ol li {
  display: block
}

ol li:before {
  content: counter(item) ". ";
  counter-increment: item;
  font-weight: bold;
}

.x-button {
  position: absolute;
  cursor: pointer;
  top: 0.5rem;
  right: 1rem;
  color: #777777;
}

.x-button:hover {
  color: red;
}

